import { FC, useState } from "react";
import { useNavigate } from "react-router-dom";
import { FieldValues, SubmitHandler, useForm } from "react-hook-form";
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import {
  useCreateProductMutation,
  useCreateCategoryMutation,
  useGetAllCategoriesQuery
} from "../../redux/api";

import 'react-tabs/style/react-tabs.css';

import cn from "classnames"
import s from "./CreateProductForm.module.scss";


const tabs = [
  { id: 0, label: " " },
  { id: 1, label: " " },
]

type Info = {
  title: string,
  description: string,
  number: number
}[]


export const CreateProductForm: FC = () => {

  const navigate = useNavigate()
  const [tabIndex, setTabIndex] = useState(0);

  const [type, setType] = useState("")
  const [info, setInfo] = useState<Info>([{ title: "", description: "", number: Date.now() }])

  const { data: categories } = useGetAllCategoriesQuery()
  const [fetchCreateProduct] = useCreateProductMutation()
  const [fetchCreateType] = useCreateCategoryMutation()


  const { register, handleSubmit, reset } = useForm()


  const addInfo = () => {
    setInfo([...info, { title: "", description: "", number: Date.now() }])
  }

  const removeInfo = (number: number) => {
    setInfo(info.filter(i => i.number !== number))
  }

  const changeInfo = (key: string, value: string, number: number) => {
    setInfo(info.map(i => i.number === number ? { ...i, [key]: value } : i))
  }

  const submitFrom: SubmitHandler<FieldValues> = async (data) => {
    const file = data.file[0]
    try {
      const formData = new FormData()
      formData.append("name", data.name)
      formData.append("price", data.price)
      formData.append("image", file)
      formData.append("typeId", data.type)
      formData.append("info", JSON.stringify(info))
      await fetchCreateProduct(formData).unwrap()
        .then((payload: any) => console.log('fulfilled', payload))
        .catch((error) => console.error('rejected', error))
      setInfo([]);
      reset();
      setTimeout(() => {
        navigate("/")
      }, 1000)
    } catch (error) {
      console.warn(error)
      alert("   !")
    }
  }

  const createType = async () => {
    await fetchCreateType({ name: type }).unwrap()
      .then((payload) => console.log('fulfilled', payload))
      .catch((error) => console.error('rejected', error))
    setType('')
    setTimeout(() => {
      navigate("/")
    }, 1000)
  }

  return (
    <section className={s.container}>
      <Tabs selectedIndex={tabIndex} onSelect={(index) => setTabIndex(index)}>
        <TabList className={s.tabList} >
          {tabs.map((tab) => {
            return (
              <Tab
                key={tab.id}
                className={cn(s.title, {
                  [s.selected]: tab.id === tabIndex
                })}>
                {tab.label}
              </Tab>
            )
          })}
        </TabList>
        <TabPanel>
          <form
            className={s.form}
            onSubmit={handleSubmit(submitFrom)}
          >
            <input
              className={s.input}
              {...register("name")}
              placeholder=""
            />
            <select
              {...register("type")} className={s.input}
            >
              <option value=""> </option>
              {
                categories?.map(({ id, name }) => {
                  return <option key={id} value={id}>{name}</option>
                })
              }
            </select>
            <input
              type="number"
              className={s.input}
              {...register("price")}
              placeholder=""
            />
            <input
              type="file"
              {...register("file")}
            />
            <div className={s.characteristic}>
              <h3> :</h3>
              {
                info.map((i) => {
                  return (
                    <div key={i.number} className={s.flex}>
                      <input
                        name="title"
                        className={s.input}
                        value={i.title}
                        onChange={(e) => changeInfo("title", e.target.value, i.number)}
                        placeholder=" "
                      />
                      <input
                        name="description"
                        className={s.input}
                        value={i.description}
                        onChange={(e) => changeInfo("description", e.target.value, i.number)}
                        placeholder=" "
                      />
                      <div className={s.delete} >
                        <button
                          type='button'
                          onClick={() =>
                            removeInfo(i.number)} className={cn(s.deleteBtn, s.button)}>
                           
                        </button>
                      </div>
                    </div>
                  )
                })
              }
              <button type='button' onClick={addInfo} className={s.button}>
                 
              </button>
            </div>
            <button type="submit" className={s.button}>
              
            </button>
          </form>
        </TabPanel>
        <TabPanel className={s.flexCol}>
          <input
            type="text"
            value={type}
            onChange={(e) => setType(e.target.value)}
            className={s.input}
          />
          <button onClick={createType} className={s.button}></button>
        </TabPanel>
      </Tabs>
    </section >
  );
};
